.competition,
.myAccount,
.helpPages {
  .navSection {
    .dropdownList {
      h4 {
        padding: 0.5rem 0;
        margin: 0;
        color: $bg-brand;
        @include subheaderH;
        border-bottom: 1px solid #929fb6;
      }

      ul {
        padding: 0;
        margin: 0 0 1rem 0;
        list-style: none;
        li {
          padding: 0.5rem 0;
          border-bottom: 1px solid #d4dbe5;
          &:last-child {
            border-bottom: 0;
          }
          a {
            color: $bg-brand;
            @include linkH;
          }
        }
      }
    }
  }
}

.myAccount,
.helpPages {
  display: flex;
  justify-content: space-between;
  list-style: none;
  flex-flow: row wrap;
  .introHeading {
    display: none;
  }
  @include media-breakpoint-up(md) {
    flex-flow: row;
  }
  //Removed * 0.5 as that halves the grid. It's 12px on each side, so 24px in total - gw
  column-gap: calc(var(--bs-gutter-x));
  row-gap: calc(var(--bs-gutter-x));

  .leftNav {
    order: 2;
    width: 100%;

    @include media-breakpoint-up(md) {
      order: 1;
      min-width: $left-nav-width;
      width: auto;
    }
  }

  .myAccountWrap,
  .helpWrap {
    order: 1;
    width: 100%;
    margin-bottom: 2rem;

    @include media-breakpoint-up(md) {
      order: 2;
    }

    .myAccountHeader,
    .helpHeader {
      h2 {
        @include subheaderH;
        color: $bg-brand;
        border-bottom: 1px solid #929fb6;
        text-align: center;
        padding: 1rem;
        margin-bottom: 0;
        background-color: #eceff6;
      }
      > p,
      .addressText {
        @include paragraphH;
        color: $bg-brand;
        text-align: center;
        padding: 1rem;
        margin-bottom: 1rem;
        background-color: #eceff6;
      }
      .mainDescription {
        p {
          @include paragraphH;
          color: $bg-brand;
          text-align: center;
          padding: 1rem;
          margin-bottom: 1rem;
          background-color: #eceff6;
        }
      }
      h2.mainHeading {
        display: none;
      }
      .myAccountOptions {
        .myAccountIconWrap {
          background-color: #ebeff5;
          flex-grow: 1;
          width: 100%;
          height: 100%;
          padding: 1.5rem;
          .imageIcon {
            width: 60px;
            height: 60px;
            display: inline-block;
            background-repeat: no-repeat;
            background-position: center;
            scale: 1;
            @include transition(all 0.35s ease);
          }
          &:hover {
            .imageIcon {
              scale: 1.1;
            }
          }
          &.orderHistory {
            .imageIcon {
              background-image: url("/Content/system/images/my-account-icons/order-history.svg");
            }
          }
          &.address {
            .imageIcon {
              background-image: url("/Content/system/images/my-account-icons/address-book.svg");
            }
          }
          &.password {
            .imageIcon {
              background-image: url("/Content/system/images/my-account-icons/password.svg");
            }
          }
          &.update {
            .imageIcon {
              background-image: url("/Content/system/images/my-account-icons/my-account.svg");
            }
          }
          h3 {
            @include subheaderH;
            color: $bg-brand;
            border-bottom: 1px solid #929fb6;
            padding-bottom: 0.5rem;
            margin: 0.5rem -1.5rem;
          }
          a {
            display: block;
            color: $bg-brand;
            text-align: center;
            width: 100%;
            &:hover {
              text-decoration: none;
            }
          }
        }
      }
    }
    .myAccountMain {
      > p {
        font-size: 1.25rem;
        color: $bg-brand;
        text-align: center;
        padding: 1rem;
        margin-bottom: 1rem;
        background-color: #eceff6;
      }
      .changeEmailAddress {
        h2 {
          font-size: 1.25rem !important;
          font-weight: 700;
          color: #003087;
          text-align: center;
          padding: 1rem;
          margin-bottom: -0.5rem !important;
          background-color: #eceff6;
        }
        .register {
          border: 2px solid $subtle-background;
          padding: 1.5rem 6.25%;
          @include breakpt-md {
            padding: 1.5rem 6.25%;
          }
        }
      }
      > form,
      > .form,
      .changeDetails {
        border: 2px solid $subtle-background;
        padding: 1.5rem 6.25%;
        @include breakpt-md {
          padding: 1.5rem 6.25%;
        }
        margin-bottom: 1rem;
        margin-top: -1rem;
        > h2 {
          font-size: 22px;
          color: $bg-brand;
        }
      }
      .changeEmailAddress {
        margin-top: 0;
      }
      .myAccountSection {
        margin-bottom: 1rem;
        .btn {
          margin-top: 1rem;
        }
      }
    }
    .myAccountContents > .myAccountContents {
      display: none;
    }
  }

  label {
    margin-top: 0.5rem;
    color: $bg-brand;
    font-weight: bold;
  }
  input[type="text"],
  input[type="password"],
  select,
  textarea {
    display: block;
    margin: 0.5rem 0;
    border: 2px solid #bcc8de;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: var(--bs-body-color);
    background-color: var(--bs-body-bg);
    background-clip: padding-box;
    border-radius: 0;
  }

  .btn,
  button {
    @include button-variant($btn-background, $white, $btn-background);
    margin-top: 0.5rem;
  }

  ul {
    list-style: none;
    padding-left: 0;
  }
}

/* Form Aligmenments */

.myAccountMain {
  .form-group {
    margin-bottom: 0.5rem;
    @include breakpt-lg {
      margin-bottom: 0;
      display: grid;
      grid-template-columns: [labels] auto [controls] 1fr;
      grid-auto-flow: row;
      grid-row-gap: 0rem;
      grid-column-gap: 1.5rem;
      &.eAdd,
      .pass {
        .field-validation-error {
          grid-area: 2 / 2;
        }
      }
      label {
        width: var(--formLabelSpace);
      }
      .emailAddress {
        margin: 0.5rem 0;
      }
      .field-validation-valid {
        padding-left: 0;
      }
      .field-validation-error {
        grid-column: controls;
      }
      .custom-control {
        padding-left: 1.5rem;
      }
      .custom-control,
      .field-validation-valid {
        position: relative;
        display: block;
        min-height: 1.5rem;
        grid-column: controls;
        grid-row: auto;
        border: none;
      }
    }
  }
  .rememberMe {
    margin-bottom: 0.5rem;
    @include breakpt-lg {
      margin-bottom: 0;
      display: grid !important;
      grid-template-columns: [labels] auto [controls] 1fr;
      grid-auto-flow: row;
      grid-row-gap: 0rem;
      grid-column-gap: 1.5rem;
      margin-left: var(--formLabelSpace);
      label {
        grid-area: 2 / 2;
      }
    }
  }
  .updateAddress,
  .submit {
    @include breakpt-lg {
      margin-left: calc(var(--formLabelSpace) + 1.5rem);
    }
  }
}

.myAccountMain {
  #addressLists {
    .defaultAddressHeader,
    .additionalAddressHeader {
      font-size: 1.25rem !important;
      font-weight: 700;
      color: #003087;
      text-align: center;
      padding: 1rem;
      margin-bottom: -1rem !important;
      background-color: #eceff6;
    }
    .deliveryAddress,
    .billingAddress {
      border: 2px solid $subtle-background;
      padding: 1.5rem 6.25%;
      @include breakpt-md {
        padding: 1.5rem 6.25%;
      }
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      gap: 0;
      grid-template-areas:
        "addressText addressText addressText buttonTop"
        "addressText addressText addressText buttonBottom";
      margin-bottom: 1rem;
      > ul {
        grid-area: addressText;
        margin-top: 1rem;
      }
      .editRemove {
        grid-area: buttonTop;
        a {
          @include button-variant($white, $btn-background, $btn-background);
          max-width: 100%;
          width: 100%;
        }
      }
      .defaultAddressButton,
      form {
        grid-area: buttonBottom;
        span {
          display: block;
          color: #35b834;
          width: 100%;
          font-size: 1.25rem;
          font-weight: 700;
          border-radius: 0;
          text-decoration: none;
          text-align: center;
          &::before {
            background: #35b834;
            -webkit-mask: url(data:image/svg+xml;base64,);
            mask: url(data:image/svg+xml;base64,);
            -webkit-mask-size: cover;
            mask-size: cover;
            content: "";
            width: 17px;
            margin-right: 3px;
            height: 15px;
            display: inline-block;
          }
        }
        button {
          @include button-variant($white, $btn-background, $btn-background);
          max-width: 100%;
          width: 100%;
        }
      }
    }
  }
  .delivery-addresses {
    > h5 {
      display: none;
    }
  }
  .billing-addresses {
    > h5 {
      font-size: 1.25rem !important;
      font-weight: 700;
      color: #003087;
      text-align: center;
      padding: 1rem;
      margin-bottom: 0;
      margin-bottom: -1rem !important;
      background-color: #eceff6;
    }
  }
}
#addressBook {
  .myAccountHeader {
    margin-bottom: 1rem;
  }

  #addAddressButton {
    margin-top: -2rem;
    background: $subtle-background;
    padding: 1rem;
    text-align: center;
    .button {
      @include button-variant($btn-background, $white, $btn-background);
      margin: 0 auto;
    }
  }
}
.myAccount,
.form-group,
.inputFields {
  label:has(~ input[data-val-required]):after {
    content: " *";
  }
}

.manual-breadcrumb {
  #breadcrumb {
    .breadcrumb {
      &__list {
        gap: 0;
        padding: 0;
      }

      &__item:not(:last-of-type)::after {
        padding: 0 0.5rem;
      }
    }
  }
}

#orderHistory {
  .myAccountMain {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    > ul {
      li.header {
        margin-left: 0;
        margin-right: 0;
        padding: 0;
        font-size: 1.25rem;
        color: $bg-brand;
        border-bottom: 2px solid #929fb6;
        background-color: #eceff6;
        > ul {
          display: flex;
          flex-wrap: wrap;
          margin: 0;
          grid-column-gap: 0;

          li {
            position: relative;
            width: 100%;
            min-height: 1px;
            padding: 15px;
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
          }
        }
      }

      li.history {
        margin-left: 0;
        margin-right: 0;
        padding: 0;
        font-size: 1.25rem;
        color: $bg-brand;
        border-bottom: 1px solid #929fb6;
        background-color: #eceff6;
        ul.orderDetails {
          display: flex;
          flex-wrap: wrap;
          margin: 0;
          grid-column-gap: 0;

          > li {
            position: relative;
            width: 100%;
            min-height: 1px;
            padding: 15px;
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
          }

          .order {
            a {
              display: inline-block;
              font-weight: 400;
              color: #3a3a3a;
              text-align: center;
              vertical-align: middle;
              cursor: pointer;
              user-select: none;
              background-color: transparent;
              border: 1px solid transparent;
              padding: 0.375rem 0.75rem;
              font-size: 1rem;
              line-height: 1.5;
              transition: color 0.15s ease-in-out,
                background-color 0.15s ease-in-out,
                border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
              color: #fff;
              background-color: #1c4aa0;
              width: 100%;
              padding: 0.375rem 0.25rem;
              margin-bottom: 0;
            }
          }

          .images {
            display: none;
          }
        }

        .prevOrderDetails {
          background-color: #ffffff;
          margin-top: 10px;
          border: 2px solid $subtle-background;
          p {
            margin-bottom: 0;
            span {
              font-weight: normal;
              color: #333;
            }
          }

          .orderDetails {
            padding: 1rem 1.5rem;
            h5 {
              font-size: 22px;
              color: $bg-brand;
              border-bottom: 1px solid #929fb6;
              padding-bottom: 1rem;
              padding-top: 0.5rem;
              margin-bottom: 1rem;
            }
            .supported {
              > p {
                text-transform: capitalize;
                margin-bottom: 0;
                span {
                  font-weight: normal;
                  color: #333;
                }

                &::before {
                  content: "Supported ";
                }
              }
            }
          }

          .item {
            padding: 0 1.5rem;
            position: relative;
            margin-bottom: 1rem;

            .itemWrap {
              > ul {
                @include media-breakpoint-up(sm) {
                  grid-template-columns: 120px calc(100% - 210px) 70px;
                }

                display: grid;
                display: -ms-grid;
                grid-gap: 0;
                -ms-grid-columns: 80px calc(100% - 170px) 70px;
                grid-template-columns: 80px calc(100% - 170px) 70px;
                grid-template-rows: repeat(3, min-content);
                grid-column-gap: 10px;
                grid-row-gap: 0;
                grid-template-areas:
                  "jacket title blank"
                  "jacket price blank"
                  "jacket status blank";

                p {
                  margin-bottom: 0;
                  color: #333;
                  font-size: 1rem;
                }

                .itemImage {
                  order: 1;
                  grid-area: jacket;
                  img {
                    max-height: 100px;
                  }
                }

                .itemInfo {
                  order: 2;
                  grid-area: title;
                  font-family: "proxima-nova", arial, sans-serif;
                  h6 {
                    margin-bottom: 0;
                  }
                }

                .status {
                  order: 2;
                  grid-area: status;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 15px;
                  p {
                    margin-bottom: 0;
                    color: #333;
                    font-size: 15px;
                  }
                  .orderStatus {
                    font-size: 15px;
                    display: block;
                    width: 100%;

                    &:before {
                      content: "Status:";
                      display: inline-block;
                      font-size: 1rem;
                      margin-right: 5px;
                      font-weight: bold;
                    }
                  }
                  .btn {
                    margin-top: 5px;
                    padding: 2px 18px;
                  }
                  .orderLineDisabledCancel {
                    display: none;
                  }
                }

                .priceQuantity {
                  order: 2;
                  grid-area: price;

                  .price {
                    width: 100%;
                    font-size: 1rem;
                    font-weight: normal;

                    &:before {
                      content: "Price:";
                      display: inline-block;
                      margin-right: 5px;
                      font-weight: bold;
                    }
                  }

                  .quantity {
                    width: 100%;
                    font-size: 1rem;
                    font-weight: normal;

                    &:before {
                      content: "Quantity:";
                      display: inline-block;
                      margin-right: 5px;
                      font-weight: bold;
                    }
                  }

                  .subtotal {
                    width: 100%;
                    font-size: 1rem;
                    font-weight: normal;

                    &:before {
                      content: "Subtotal:";
                      display: inline-block;
                      margin-right: 5px;
                      font-weight: bold;
                    }
                  }
                }
              }
            }
          }

          div.orderTotal {
            .paymentDelivery {
              margin-bottom: 1rem;
              &::before {
                content: "Payment Delivery";
                display: block;
                background-color: #ebeff5;
                color: $bg-brand;
                padding: 1rem 1.5rem;
                margin-bottom: 1rem;
              }
              p,
              .deliverySmsNumber,
              .deliverySafePlace {
                padding: 0 1.5rem;
              }
            }
            .pricing {
              margin-bottom: 1rem;
              &::before {
                content: "Pricing";
                display: block;
                background-color: #ebeff5;
                color: $bg-brand;
                padding: 1rem 1.5rem;
                margin-bottom: 1rem;
              }
              p {
                padding: 0 1.5rem;
              }
            }
          }
        }
      }
    }
  }
}
#passwordReset {
  .myAccountContents {
    .myAccountHeader {
      display: none;
    }
  }
}
#register {
  display: block !important;
  .leftNav {
    display: none;
  }
  .myAccountWrap {
    .myAccountContents {
      .myAccountHeader {
        h2:first-child {
          display: none;
        }
      }
      .myAccountMain {
        border: 2px solid #eceff6;
        padding: 1.5rem 6.25%;
        margin-bottom: 1rem;
        #checkout {
          width: 100%;
          display: block;
        }
      }
    }
  }
}

#logonRegister {
  display: block !important;
  .leftNav {
    display: none;
  }
  .myAccountWrap {
    width: 100%;
    display: block;
    .myAccountContents {
      .myAccountHeader {
        display: none;
      }
      .myAccountMain {
        @include media-breakpoint-up(md) {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: stretch;
          align-content: flex-end;
          gap: 1rem;
        }
        h4 {
          font-size: 22px;
          color: $bg-brand;
        }
        button {
          width: 100%;
          max-width: 100%;
        }
        .customerReturning {
          width: 100%;
          border: 2px solid $bg-brand;
          form {
            padding: 1.5rem 6.25%;
            padding-top: 0.5rem !important;
            @include breakpt-md {
              padding: 1.5rem 6.25%;
            }
          }
          h4 {
            background: $subtle-background;
            padding: 1rem 0;
            @include mainheaderH;
            color: $bg-brand;
            text-align: center;
          }
          .field-validation-valid {
            display: none;
          }
          .loginSignUpButtons {
          }
          .forgottenPassword {
            margin-top: 0.5rem;
            display: block !important;
            text-align: center;
          }
          button {
            margin-top: 1rem;
          }
        }
        .newRegistration {
          width: 100%;
          border: 2px solid $bg-brand;
          .benefitsblock {
            display: none;
          }
          h4 {
            background: $subtle-background;
            padding: 1rem 0;
            @include mainheaderH;
            color: $bg-brand;
            text-align: center;
          }
          p,
          .guestRegistration {
            text-align: center;
            margin: 1.5rem 6.25%;
            @include breakpt-md {
              margin: 1.5rem 6.25%;
            }
          }
          .benefitsblock {
            margin-bottom: 1rem;
            padding-bottom: 1rem;
            border-bottom: 1px solid #929fb6;
            strong {
              color: $bg-brand;
            }
            p {
              margin-bottom: 0.25rem;
            }
          }
        }
      }
    }
  }
}

.wishlistItem {
  &:first-child {
    .search-item {
      border-top: 0;
    }
  }

  .search-item {
    border-top: #cac9ca 1px solid;

    .search-item__wrap {
      display: flex;
      flex-wrap: wrap;

      .search-item__image {
        flex: 0 0 10.66666667%;
        max-width: 10.66666667%;
        padding-top: 1rem;
        padding-bottom: 1rem;
        padding-left: 0;
      }

      .search-item__info {
        flex: 0 0 64.33333333%;
        max-width: 64.33333333%;
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 1rem;

        p {
          margin: 0 0 5px;

          @include media-breakpoint-up(md) {
            margin: 0.5rem 0 0;
          }
        }

        h3 {
          font-size: 17px;
          line-height: 20px;

          @include media-breakpoint-up(md) {
            font-size: 20px;
            line-height: 24px;
          }

          font-weight: 400;
          margin-bottom: 8px;

          a {
            color: #002f87;
          }
        }

        h4 {
          font-size: 1rem;
          line-height: 16px;

          @include media-breakpoint-up(md) {
            font-size: 1.25rem;
            line-height: 20px;
          }

          font-weight: 400;
          margin: 0 0 15px 0;
          display: block;

          a {
            color: #3a3a3a;
          }
        }
      }

      .search-item__purchase {
        flex: 0 0 25%;
        max-width: 25%;
        padding-left: 15px;
        padding-right: 15px;
        text-align: center;
        @include media-breakpoint-up(md) {
          background-color: #eee;
        }

        .search-item__purchase-wrap {
          padding-bottom: 1rem;

          @include media-breakpoint-up(md) {
            padding-top: 1rem;
          }

          @include media-breakpoint-up(md) {
            display: table;
            width: 100%;
            height: 100%;

            .search-item__purchase-inner {
              display: table-cell;
              vertical-align: middle;
            }
          }
        }

        .search-item__purchase-format,
        .search-item__purchase-availability {
          display: none;
        }

        .btn {
          width: 100%;
        }

        p.rrp {
          color: gray;
          margin-bottom: 10px;
        }
        .removeWishlistItem {
          color: #002f87;
          text-decoration: underline;
          margin-top: 0.25rem;
          display: block;
        }
        p.price {
          color: #002f87;
          font-size: 22px;
          @include media-breakpoint-up(md) {
            line-height: normal;
          }

          line-height: 22px;
          margin-bottom: 10px;
          font-weight: 600;
        }
      }
    }
  }
}

div#wishlistEmail {
  text-align: center;
  background-color: #1c4aa7;
  padding: 2rem;
  margin-top: 1rem;
  h3 {
    color: #fff;
  }
  h4 {
    color: #fff;
    margin-bottom: 1.5rem;
  }
  .frmShareEmail {
    display: inline-flex;
    width: 100%;
  }
  .form-control {
    margin: 0 0.5rem 0 0;
    padding: 14px;
  }
}

#addressBookEdit {
  .postcodeLookupContainer {
    #postcodeLookup {
      float: left;
      width: calc(100% - 110px);
    }
    a.btnLookupPostcode {
      float: right;
      bottom: -5px;
      position: relative;
      @include button-variant($btn-background, $white, $btn-background);
    }
  }

  .manualAddressContainer {
    display: block;
    width: 100%;
    clear: both;
    label {
      display: none;
    }
    a {
      color: #1c4aa7;
      text-decoration: underline;
    }
  }
}

.outterStretch {
  .ui-btn {
    color: #002f87;
    background-color: #eee;
    width: 100%;
    display: block;
    text-align: center;
    padding: 8px;
    margin-bottom: 1rem;
  }
}

#digitalDownload,
#mainBasketdigitalDownload {
  > ul {
    padding: 1rem;
    margin-top: 1rem;
    background-color: #eee;
  }
  .header {
    padding-bottom: 0.5rem;
    margin-bottom: 0.5rem;
    border-bottom: 1px solid #dddddd;
    ul {
      display: flex;
      flex-wrap: wrap;
      .itemCover {
        flex: 0 0 10.66666667%;
        max-width: 10.66666667%;
      }
      .itemHeader {
        flex: 0 0 52.33333333%;
        max-width: 52.33333333%;
        padding-left: 15px;
        padding-right: 15px;
        @include media-breakpoint-up(lg) {
          padding-top: 1rem;
        }
      }
      .orderDate {
        @include media-breakpoint-up(lg) {
          flex: 0 0 18.5%;
          max-width: 18.5%;
        }
        flex: 0 0 37%;
        max-width: 37%;
        padding-left: 15px;
        padding-right: 15px;
        text-align: center;
      }
      .downloadLink {
        @include media-breakpoint-up(lg) {
          flex: 0 0 18.5%;
          max-width: 18.5%;
          display: block;
        }
        flex: 0 0 0%;
        max-width: 0%;
        display: none;
        padding-left: 15px;
        padding-right: 15px;
        text-align: center;
      }
    }
  }
  .digitalTitle {
    ul {
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 0.5rem;
      margin-bottom: 0.5rem;
      border-bottom: 1px solid #dddddd;
      .itemImage {
        flex: 0 0 10.66666667%;
        max-width: 10.66666667%;
      }
      .itemInfo {
        flex: 0 0 52.33333333%;
        max-width: 52.33333333%;
        padding-left: 15px;
        padding-right: 15px;
        @include media-breakpoint-up(lg) {
          padding-top: 1rem;
        }
      }
      .orderDate {
        @include media-breakpoint-up(lg) {
          flex: 0 0 18.5%;
          max-width: 18.5%;
          padding-top: 1rem;
        }
        flex: 0 0 37%;
        max-width: 37%;
        padding-left: 15px;
        padding-right: 15px;

        text-align: center;
      }
      .downloadLink {
        @include media-breakpoint-up(lg) {
          flex: 0 0 18.5%;
          max-width: 18.5%;
          padding-left: 15px;
          padding-right: 15px;
          padding-top: 1rem;
        }
        flex: 0 0 100%;
        max-width: 100%;
        text-align: center;
        a {
          @include button-variant($btn-background, $white, $btn-background);
          width: 100%;
          display: inline-block;
          margin: 0.5rem 0;
          @include media-breakpoint-up(lg) {
            width: auto;
            margin: auto;
          }
        }
      }
    }
  }
}

.template-help {
  color: $bg-brand;
  margin-bottom: 0.75rem;
  &.show {
    .template-help__header {
      &:before {
        rotate: 90deg;
      }
    }
    .template-help--hide {
      display: block;
    }
  }
  &.hide {
    .template-help--hide {
      display: none;
    }
  }

  &__question-answer {
    position: relative;
    padding-bottom: 0.5rem;
    &::after {
      content: "";
      width: 100%;
      max-width: 30%;
      height: 2px;
      background-color: #bcc8de;
      display: block;
      position: absolute;
      bottom: 0;
    }
  }
  &__header {
    color: $bg-brand;
    font-weight: bold;
    @include largesubheaderH;
    padding-bottom: 1rem;
    margin-bottom: 0.5rem;
    position: relative;
    padding: 0.5rem 0 0.5rem 1.5rem;
    position: relative;
    cursor: pointer;
    &::before {
      content: "";
      background: url("/content/system/images/blue-right-arrow.svg") 100%
        no-repeat #fff;
      height: 23px;
      width: 16px;
      display: inline-block;
      margin-right: 0.5rem;
      position: absolute;
      left: 0;
      top: 5px;
    }
  }
}
.template-anchor-links {
  background-color: $subtle-background;
  padding: 0 1rem 1rem;
  text-align: center;
  margin-top: -1.5rem;
  margin-bottom: 1.5rem;
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    li {
      a {
        color: $bg-brand;
        width: 100%;
        display: block;
        font-weight: bold;
        padding: 0.25rem 0;
        position: relative;
      }
    }
  }
}
.template-help-section {
  &.anchored {
    h4 {
      @include smallsubheaderH;
      padding-top: 2rem;
    }
  }
  &__body {
    ul {
      list-style: disc;
      padding-left: 1rem;
    }
  }
}
.template-help {
  table {
    margin: 1rem 0;
    tr {
      &:first-child {
        th,
        td {
          background-color: $subtle-background;
          padding: 1rem;
          font-weight: bold;
        }
      }
      th,
      td {
        border-bottom: 1px solid $subtle-border;
        padding: 1rem;
      }
    }
  }
}
