/* === CHECKOUT ============================= */
:root {
  --formLabelSpace: 135px;
  --wrapperPadding: 6.25%;
  @include breakpt-xl {
    --wrapperPadding: 12.5%;
  }
}
.form-control {
  &::placeholder {
    color: #a2a5ab;
  }
}
.form-group {
  > span {
    color: #a2a5ab;
  }
}
#checkoutHeader {
  background: $subtle-background;
  > .container-md > .row {
    display: grid;
    grid-template-columns: 1fr;
    @include breakpt-md {
      grid-template-columns: 2fr 2fr 8fr;
      .checkout-header {
        grid-column: 1 / 4;
      }
      .checkoutBreadcrumb {
        grid-column: 1 / 4;
      }
    }
    @include breakpt-lg {
      .checkout-header {
        grid-column: 1 / 2;
      }
      .checkoutBreadcrumb {
        grid-column: 3 / 4;
      }
    }
  }
  .checkout-header {
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas:
      "secu"
      "logo";
    justify-content: unset;
    align-items: unset;
    gap: unset;
    padding: unset;
    gap: 1rem;
    width: 100%;
    @include breakpt-md {
      width: 220px;
      margin: 0 auto;
    }
    &__secure {
      grid-area: secu;
      display: block;
      font-weight: bold;
      text-align: center;
      font-size: 1rem;
      background: #fff;
      padding: 0.5rem 1rem;
      &::before {
        content: "";
        background-image: $iconLock;
        background-size: 20px;
        background-repeat: no-repeat;
        background-position: center;
        height: 23px;
        width: 33px;
        display: inline-block;
        position: relative;
        top: -2px;
        margin-bottom: -7px;
      }
    }
    &__logo {
      grid-area: logo;
      display: block;
      margin: 1rem 0;
      @include breakpt-lg {
        margin: 1rem 0 2rem;
      }
      img {
        max-width: 220px;
        width: 100%;
        height: auto;
        margin: 0 auto;
      }
    }
  }
  .checkoutBreadcrumb {
    display: none;
    @include breakpt-md {
      display: grid;
    }
    ul {
      counter-reset: checkoutSteps;
      place-self: center right;
      display: flex;
      align-items: center;
      margin: 0;
      padding: 0;
      width: 100%;
      li {
        counter-increment: checkoutSteps;
        flex: 1;
        min-width: fit-content;
        list-style: none;
        border-bottom: 2px solid $subtle-border;
        padding: 0.5rem 1rem 0.5rem 0;
        &.previous,
        &.current {
          border-bottom-width: 6px;
          border-color: $bg-brand;
        }
        span {
          display: flex;
          gap: 1ch;
          font-weight: bold;
          color: $bg-brand;
          align-items: center;
          &::before {
            display: block;
            content: counter(checkoutSteps);
            background: $bg-brand;
            color: #fff;
            width: 3ch;
            height: 3ch;
            aspect-ratio: 1 / 1;
            text-align: center;
            border-radius: 50%;
            font-size: 18px;
            line-height: 3ch;
          }
        }
        div {
          display: none;
        }
      }
    }
  }
}

#main {
  .checkout {
    margin-top: 2rem;
    @include breakpt-lg {
      display: grid;
      grid-template-columns: 2fr 1fr;
      gap: var(--bs-gutter-x);
    }
    a.btnClearAll,
    a.btnSearchBy,
    a.btnEnterAddress {
      color: $bg-brand;
      cursor: pointer;
      text-decoration: underline;
    }
    .requiredFields {
      font-weight: bold;
      color: $bg-brand;
      span {
        font-weight: bold;
        color: $bg-brand;
      }
    }
    .errorMessages {
      .validation-summary-errors {
        margin: 0;
        padding: 0;
        > span {
          display: block;
          color: #a81919;
          background-color: #f5e3e5;
          margin: 0;
          padding: 1rem 12.5%;
          text-align: center;
        }
      }
    }
    &.delivery,
    &.billing {
      .checkoutWrap {
        border: none;
      }
      .checkout-main-section {
        border: 3px solid $bg-brand;
      }
    }
    &.creditCard {
      > .checkoutWrap {
        position: static;
      }
      #checkoutBasket {
        display: none;
      }
      #checkoutSidebar {
        padding: 1rem;
        border-color: transparent;
        h4 {
          color: $bg-brand;
          font-weight: bold;
          margin-bottom: 1rem;
          border-bottom: 2px solid $subtle-border;
        }
        .orderSummaryWide {
          &:not(:first-child) {
            margin-top: 1rem;
          }
        }
        .orderSummaryDetailsLabel {
          color: $bg-brand;
          font-weight: bold;
        }
      }
    }
    .checkoutWrap,
    #checkoutSidebar {
      position: relative;
      border: 3px solid $bg-brand;
      margin-top: 2rem;
      @include breakpt-lg {
        position: sticky;
        top: 1rem;
        margin-top: 0;
      }
    }
    #checkoutSidebar {
      align-self: start;
      min-height: 415px;
    }
    #basketSummary {
      position: static !important;
      .checkoutBasket {
        display: flex;
        flex-direction: column;
        max-height: 59lvh;
        @include breakpt-lg {
          // 100vh - (1 rem padding + 3px border)
          max-height: calc(100lvh - (2rem + 6px));
        }
        .miniBasketHeader,
        .miniBasketFooter {
          flex: 0;
        }
        .basketItems {
          flex-shrink: 1;
          overflow-y: auto;
          min-height: 150px;
        }
      }
      .miniBasketHeader {
        background: $subtle-background;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        line-height: 1.2;
        .basketQty {
          color: $bg-brand;
          font-size: 1.5rem;
          font-weight: bold;
        }
        .totals {
          display: none;
        }
        .basketEdit {
          a {
            color: $bg-brand;
            text-decoration: underline;
          }
        }
      }
      .basketItems {
        ul {
          padding: 1rem;
          margin: 0;
          li.basketItem {
            list-style: none;
            display: grid;
            grid-template-columns: 1fr 3fr 50px;
            grid-template-rows: repeat(3, min-content);
            gap: 0.5rem 1.5rem;
            padding-bottom: 0.5rem;
            &:not(:only-child):not(:last-child) {
              border-bottom: 2px solid $subtle-border;
              margin-bottom: 1rem;
            }
            .item {
              &Thumb {
                grid-column: 1 / 2;
                grid-row: 1 / -1;
                &Img {
                  max-width: 100px;
                  max-height: 100px;
                }
              }
              &Title {
                grid-column: 2 / 3;
                grid-row: 1 / 2;
                text-wrap: pretty;
              }
              &TitlePrice {
                display: block;
                font-weight: bold;
                margin-top: 0.5rem;
              }
              &TitleQty {
                grid-column: 3 / 4;
                grid-row: 1 / 2;
                font-weight: bold;
              }
              &TitleDiscount {
                grid-column: 2 / 3;
                grid-row: 2 / 3;
              }
            }
          }
        }
      }
      .miniBasketFooter {
        background: $subtle-background;
        position: relative;
        &::before {
          content: "";
          position: absolute;
          top: 2px;
          left: 0;
          transform: translateY(-100%);
          width: 100%;
          height: 2rem;
          background: linear-gradient(
            0deg,
            rgba(255, 255, 255, 1) 10%,
            rgba(255, 255, 255, 0) 100%
          );
        }
        .subTotals {
          padding: 1rem;
          border-bottom: 2px solid $subtle-border;
          > div:not(:first-child) {
            margin-top: 0.5rem;
          }
        }
        .totals {
          color: $bg-brand;
          font-size: 1.5rem;
          font-weight: bold;
          padding: 1rem;
        }
        .subTotals > div > div,
        .totals {
          display: flex;
          justify-content: space-between;
        }
        .subTotals .delivery div {
          justify-content: flex-start;
          // gap: 0.5ch;
          .deliveryPrice {
            margin-left: auto;
          }
        }
        .subTotals .vouchers {
          .voucherAppliedCode,
          .giftCardAppliedCode {
            width: 100%;
            display: flex;
            justify-content: space-between;
          }
        }
      }
      .miniBasketFooter ~ .basketEdit {
        display: none;
      }
    }
    .checkoutSection,
    .checkoutShow {
      // --formLabelSpace: 125px;
      margin-bottom: 2rem;
      // --wrapperPadding: 6.25%;
      // @include breakpt-xl {
      //   --wrapperPadding: 12.5%;
      // }
      h4 {
        background: $subtle-background;
        color: $bg-brand;
        text-align: center;
        font-weight: bold;
        margin-bottom: 2rem;
        padding: 2rem 0;
      }
      input,
      select {
        border-radius: 0;
        &:not(.btn):not(.button) {
          border: 2px solid $subtle-border;
          padding: 0.375rem 0.75rem;
        }
      }
      label {
        color: $bg-brand;
        font-weight: bold;
      }
      .btn,
      .button {
        display: inline-block;
        background: $bg-brand;
        color: #fff;
        padding: 0.375rem 0.75rem;
        text-align: center;
        line-height: 1.5;
        vertical-align: middle;
        border: 1px solid $bg-brand;
        border-radius: 0;
        cursor: pointer;
        @include transition(background-color 0.5s ease);
        &:hover {
          background-color: $btn-hover;
          text-decoration: none;
          color: $white;
        }
        &[class*="NavBack"] {
          color: unset;
          background: $subtle-header;
          border-color: $subtle-header;
          margin-right: auto;
          @include transition(background-color 0.5s ease);
          &:hover {
            background-color: $btn-hover;
            text-decoration: none;
            color: $white;
          }
        }
      }
    }
    .payNowNavBackContainer {
      margin-right: auto;
    }
    #delivery {
      .deliveryHeader {
        .subTitle {
          display: none;
        }
      }
    }
    #deliverToResults {
      padding: 0 var(--wrapperPadding, 0);
      margin-bottom: 1rem;
    }
    #loclist {
      ul {
        padding: 0;
        border-top: 1px solid $subtle-border;
        grid-template-columns: 1fr;
        gap: 0;
        li {
          list-style-type: none;
          &:has(input:checked) {
            background: $subtle-background;
          }
        }
      }
      .whs-hours {
        padding: 24px;
        @include media-breakpoint-up(md) {
          padding: 12px;
        }
        @include media-breakpoint-up(lg) {
          padding: 24px;
        }
        &-list {
          border-top: none;
        }
      }
      .storeResult {
        position: relative;
        display: flex;
        border-bottom: 1px solid $subtle-border;
        z-index: 1;
        background-color: #fff;
        input {
          position: absolute;
          visibility: hidden;
          &:checked {
            ~ .clickBox {
              &::after {
                @include media-breakpoint-up(xxl) {
                  content: "Selected";
                }
              }
            }
            ~ .radioBox {
              background: $bg-brand;
              .check {
                background: transparent;
              }
            }
          }
        }
        label.clickBox {
          position: absolute;
          display: block;
          z-index: 3;
          width: 100%;
          height: 100%;
          @include media-breakpoint-up(xxl) {
            position: absolute;
            display: block;
            inset: 0;
            background-color: transparent;
            cursor: pointer;
            z-index: 2;
            &::after {
              content: "Select";
              display: block;
              position: absolute;
              right: 0.5rem;
              bottom: 0.5rem;
              background: $bg-brand;
              color: #fff;
              font-weight: normal;
              padding: 0.25rem 0.75rem;
            }
          }
        }

        .radioBox {
          flex: 0 0 32px;
          display: grid;
          place-items: center center;
          background: $subtle-background;
          .check {
            height: 20px;
            width: 20px;
            border-radius: 50%;
            background: #fff;
            border: 5px solid #fff;
          }
        }
        .smallText {
          // position: relative;
          display: grid;
          grid-template-columns: auto 1fr 105px;
          gap: 0 0.75rem;
          flex: 1 1 100%;
          padding: 0.5rem 0.75rem;
          // z-index: 1;
          > div {
            grid-column: 2 / 4;
            @include media-breakpoint-up(sm) {
              grid-column: 2 / 3;
            }
          }
          .storeResultName {
            grid-column: 2 / 4;
            display: flex;
            position: relative;
            padding-top: 25px;
            span {
              position: absolute;
              color: #1ba6ad;
              left: 0;
              top: 0;

              @include media-breakpoint-up(sm) {
                text-align: right;
                top: unset;
                left: unset;
                right: 0;
              }

              display: flex;
              align-items: center;
              color: $bg-accent;
              &::before {
                content: "";
                display: inline-block;
                width: 1rem;
                height: 1rem;
                aspect-ratio: 1 / 1;
                margin-right: 0.5ch;
                background: url("/content/system/images/map-pin.svg") center
                  100% no-repeat;
              }
            }
            @include media-breakpoint-up(sm) {
              align-items: center;
              justify-content: space-between;
              position: relative;
              padding-right: 100px;
              padding-top: 0;
              span {
                right: 10px;

                top: unset;
                left: unset;
              }
            }
          }
          &::before {
            content: "";
            grid-column: 1 / 2;
            grid-row: 1 / 4;
            place-self: center center;
            display: block;
            width: 30px;
            height: 30px;
            background: url("/content/system/images/product-icons/icon-click.svg")
              center / 100% no-repeat;
            @include media-breakpoint-up(sm) {
              width: 35px;
              height: 35px;
            }
          }

          .storeMapViewDetails {
            position: relative;
            margin: 0;
            z-index: 5;
            a {
              font-size: 0.9em;
              color: $bg-brand;
              text-decoration: underline;
              cursor: pointer;
              display: block;
            }
            &.displayNone {
              display: block !important;
            }
            &.checkoutShow {
              display: block;
            }
          }
        }
        &Map {
          display: none;
          margin: 0;
          &.checkoutShow {
            display: grid !important;
            grid-template-columns: 1fr;
            border-bottom: 1px solid #bcc8de;
            @include breakpt-lg {
              grid-template-columns: 5fr 5fr;
            }
            @include breakpt-xxl {
              grid-template-columns: 6fr 4fr;
            }
          }
          .store {
            &DetailsDiv {
              @include breakpt-lg {
                grid-column: 2 / 3;
                grid-row: 1 / 2;
              }
            }
            &DetailsImgDiv {
              display: none;
            }
            &DetailsTextDiv {
              display: grid;
              grid-template-columns: repeat(2, 1fr);
              gap: 0.5rem 1.5rem;
              padding: 0.5rem 1rem;
              background: $subtle-background;
              > div:first-child {
                grid-column: 1 / -1;
              }
            }
            &MapDiv {
              #map_canvas {
                @include breakpt-lg {
                  height: 100% !important;
                }
              }
            }
          }
        }
      }
    }
    #storeDelivery {
      margin-bottom: 3rem;
      .storeSearchParameters {
        display: grid;
        grid-template-columns: 1fr var(--formLabelSpace);
        gap: 0.5rem 1.5rem;
        margin-bottom: 2rem;
        @include breakpt-lg {
          grid-template-columns: auto var(--formLabelSpace);
        }
        label {
          grid-column: 1 / -1;
        }
      }
    }
    #storeDeliveryCustomerName {
      .subTitle {
        display: none;
      }
      .inputFields {
        ul {
          padding: 0;
          li {
            list-style-type: none;
            display: grid;
            grid-template-columns: 1fr;
            gap: 0rem 1rem;
            @include breakpt-lg {
              grid-template-columns: var(--formLabelSpace) auto;
            }
            select {
              justify-self: start;
            }
            + li {
              margin-top: 1rem;
            }
          }
        }
      }
    }
    #deliveryMethod {
      #deliveryOptions {
        padding: 1rem var(--wrapperPadding, 0);
      }
      .deliveryOptionsLabel {
        display: none;
      }
      .deliveryMethods {

        .deliveryOption {
          position: relative;
          display: flex;
          border-bottom: 1px solid $subtle-border;
          z-index: 1;
          &:has(input:checked) {
            background: $subtle-background;
          }
          input {
            position: absolute;
            visibility: hidden;
          }
          .description {
            font-weight: normal;
            clear: both;
            display: block;
          }
        }
      }
    }
    #deliveryDetails {
      #deliveryDetails {
        padding: 0 var(--wrapperPadding, 0) 1rem;
        .form-group {
          margin-top: 2rem;
          .subTitle,
          .deliverySafePlaceDesc {
            grid-column: 2 / 3;
            grid-row: 2;
            color: #9d9d9d;
          }
          .subTitle {
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;
            @include breakpt-lg {
              padding-top: 0.5rem;
              padding-bottom: 0rem;
            }
          }
        }
        .subTitle,
        .deliverySafePlaceDesc {
          font-size: 0.9em;
        }
      }

      .form-group:has(input[name="DeliverySmsNumber"]) {
        label {
          grid-row: 1;
        }

        .field-validation-error {
          grid-row: 3 / 4 !important;
        }
      }
    }
    #homeDelivery {
      // #addressBookSelector {
      //   margin-bottom: 2rem;
      // }
      // #deliveryAddressSelect {
      //   margin-bottom: 2rem;
      //   .form-group {
      //     display: flex;
      //     flex-direction: column;

      //     label {
      //       text-wrap: pretty;
      //       text-align: left;
      //     }
      //     select {
      //       max-width: 100%;
      //       margin-top: 0.5rem;
      //     }
      //   }
      // }
      .newDeliveryAddressWrapper,
      #addNewDeliveryAddress {
        #DeliveryAddress_UserTitlePrefixTypeId {
          justify-self: start;
          min-width: 40%;
        }
        #rememberAddress {
          display: block;
        }
      }
      // .requiredFields,
      // .enterAddressContainer.checkoutShow,
      // .searchByContainer.checkoutShow,
      // .clearAllContainer {
      //   display: grid !important;
      //   grid-template-columns: var(--formLabelSpace) auto;
      //   gap: 0rem 1.5rem;
      //   margin: 1rem 0;
      //   > * {
      //     grid-column: 1 / -1;
      //     @include breakpt-lg {
      //       grid-column: 2 / 3;
      //     }
      //   }
      // }
      .manualAddress {
        .form-group {
          &:not(:first-child) {
            margin-top: 1rem;
          }
        }
      }
      #gifting {
        padding: 2rem var(--wrapperPadding, 0);
        margin-bottom: 2rem;
        background: $subtle-background;
        .form-group {
          &:not(:first-child) {
            margin-top: 1rem;
          }
        }
        span {
          display: block;
        }
        #giftMessage {
          margin-top: 1rem;
        }
        .custom-checkbox {
          grid-template-columns: 0;
          gap: 0;
          display: block;
        }
      }
    }
    // .postcodeLookupContainer.checkoutShow {
    //   display: grid !important;
    //   grid-template-columns: 1fr;
    //   align-items: start;
    //   gap: 0.5rem 1rem;
    //   margin-bottom: 2rem;
    //   @include breakpt-lg {
    //     grid-template-columns: var(--formLabelSpace) 3fr 2fr;
    //   }
    //   a {
    //     border: none;
    //     padding: 0.375rem 0.75rem !important;
    //     max-width: 100%;
    //   }
    // }
    // .postcodeLookupContainer {
    //   grid-template-columns: 1fr;
    //   grid-template-rows: repeat(3, auto);
    //   align-items: baseline;
    //   @include breakpt-lg {
    //     grid-template-columns: var(--formLabelSpace) 1fr auto;
    //     grid-template-rows: repeat(2, auto);
    //   }
    //   label {
    //     grid-column: 1 / 3;
    //     @include breakpt-lg {
    //       grid-column: unset;
    //     }
    //   }
    //   .button {
    //     grid-column: 2 / 3;
    //     @include button-variant($btn-background, #fff, transparent);
    //     border-radius: 0;
    //     cursor: pointer;
    //     @include breakpt-lg {
    //       grid-column: unset;
    //     }
    //   }
    // }
    .inputFields,
    .navigationButtons,
    #deliveryOptions,
    #gifting {
      padding: 0 var(--wrapperPadding, 0);
    }
    .navigationButtons {
      display: flex;
      gap: 1rem;
      margin-top: 2rem;
      align-items: flex-start;
      flex-direction: row;
      font-size: 1rem;

      .btn-primary,
      .storeDeliveryNavContinue,
      .btnNavigate {
        width: auto;
        max-width: auto;
        font-size: 1.25rem;
        padding: 0.375rem;
        @media (min-width: 400px) {
          font-size: 1.25rem;
          padding: 0.375rem 0.75rem;
        }
      }
      .btn-primary,
      .storeDeliveryNavContinue {
        @include breakpt-lg {
          padding: 15px 35px;
        }
      }
    }
    .safePlace {
      > .deliverySafePlaceDesc {
        display: none;
      }
    }

    .billingContainer {
      display: none;
      margin-bottom: 1rem;
      &.billingShow {
        display: block;
      }
      .radioOptions {
        padding: 0 var(--wrapperPadding, 0);
        margin-bottom: 1rem;
      }
    }
    #billingDetails {
      .paymentContainer {
        > div {
          padding: 0 var(--wrapperPadding, 0);
        }
        .paymentTypeContainer {
          margin-top: 2rem;
        }
      }
      .addVoucherOption {
        .custom-control {
          padding: 0;
        }
        display: none;
        &.voucherToggle {
          display: block;
        }
      }
      #applyGiftCard {
        position: relative;
        margin-top: 1rem;
        @supports (grid-template-rows: subgrid) {
          @include breakpt-lg {
            display: grid;
            grid-template-columns: 5fr 2fr 3fr;
            grid-template-rows: min-content min-content;
            gap: 0.5rem 1.5rem;
            .form-group {
              grid-template-columns: subgrid !important;
              grid-template-rows: subgrid !important;
              gap: 0.5rem 0.75rem !important;
              grid-column: 1 / -1;
              grid-row: 1 / -1;
              margin-bottom: 0 !important;
              .voucherLabel {
                margin-bottom: 0 !important;
                &[for="GiftCardNumber"] {
                  grid-column: 1 / 2;
                  grid-row: 1 / 2;
                }
