/* Template Styles */

/* === TOPBAR ============================= */
body {
  > .top-bar {
    background-color: $bg-brand-dark;
    color: #fff;
    font-weight: 600;
    font-size: 18px;
    a {
      padding: 0 21px;
      position: relative;
      z-index: 1;
    }
    .col-12 {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .top-bar__text {
      width: 100%;
      text-align: center;
    }
    .top-bar__close {
      cursor: pointer;
      position: absolute;
      right: 1rem;
      z-index: 2;
      padding: 0.5rem;
      svg {
        width: 20px;
        height: 20px;
      }
    }
  }
}

/* Text Image Block */
:not(.smaller-template-content) {
  .template-container {
    @extend .container-md;
  }
  .template-row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(-0.5 * var(--bs-gutter-x));
    margin-left: calc(-0.5 * var(--bs-gutter-x));
    > * {
      flex-shrink: 0;
      max-width: 100%;
      padding-right: calc(var(--bs-gutter-x) * 0.5);
      padding-left: calc(var(--bs-gutter-x) * 0.5);
      margin-top: var(--bs-gutter-y);
    }
  }
}

.tm {
  margin-bottom: 2rem;
  @include media-breakpoint-up(md) {
    margin-bottom: 3rem;
  }
  &--half {
    margin-bottom: 1rem;
    @include media-breakpoint-up(md) {
      margin-bottom: 1.5rem;
    }
  }
  &--double {
    margin-bottom: 4rem;
    @include media-breakpoint-up(md) {
      margin-bottom: 5rem;
    }
  }
  hr {
    margin-bottom: 1rem;
    margin-top: 1rem;
    color: #fff;
  }
}

.text-image-block {
  &__header h2 {
    font: 600 1.2rem Gotham-Bold, tahoma, Arial, sans-serif;
    font-size: 22px;
    text-align: center;
    margin-bottom: 20px;
    line-height: 18px;
    color: #333333;
  }
  &__content {
    -moz-box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.1);
    box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    overflow: hidden;
    margin: 1rem 0;
  }
  &__subtext {
    font: 400 13px Gotham-Book, Arial, sans-serif !important;
    padding: 11px 10px 15px 10px;
    margin-bottom: 0;
    height: 60px;
  }
}

.link-block {
  &__link {
    color: $bg-brand;
    font-weight: 600;
    padding: 0.75rem;
    border: 1px solid $bg-brand;
    width: 100%;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow: hidden;
    position: relative;
    z-index: 1;
    text-decoration: none;
    border-radius: 10px;
    transition: color 0.35s ease;
    &::after {
      content: "";
      width: 0;
      left: 0;
      top: 0;
      height: 100%;
      background-color: #013088;
      color: #fff;
      transition: width 0.2s ease;
      z-index: -1;
      position: absolute;
    }
    &:hover {
      color: #fff;
      text-decoration: none;
      &::after {
        width: 100%;
      }
    }
  }
}

.blue-faded {
  .link-block {
    &__link {
      background-color: $subtle-background;
    }
  }
}

.img-banner {
  position: relative;
  &.fw-bg {
    .glide__slide {
      width: 100%;
    }
  }
  .single-img {
    margin-left: auto;
    margin-right: auto;
  }
  .two-col {
    margin-left: auto;
    margin-right: auto;
    padding: 0rem 3rem;
    margin-bottom: 0;
    display: flex;
    -moz-column-gap: 1rem;
    column-gap: 1rem;
    flex-direction: row;
    align-items: center;
    @include media-breakpoint-up(md) {
    }
    &__left {
      width: 50%;
    }
    &__right {
      width: 50%;
      padding: 0.5rem 0;
      @include media-breakpoint-up(md) {
        padding-left: 1rem;
      }
      a {
        color: #fff;
      }
      h6 {
        font-size: 22px;
        @include media-breakpoint-up(sm) {
          font-size: 26px;
        }
        @include media-breakpoint-up(md) {
          font-size: 36px;
        }
        color: #fff;
      }
      p {
        font-size: 1.25rem;
        @include media-breakpoint-up(md) {
          font-size: 22px;
        }
        color: #fff;
        text-decoration: underline;
      }
    }
  }
  &__main {
    position: relative;
  }
  .glide__arrows {
    @include container();
    position: absolute;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
  }
  .glide__arrow--right {
    right: 1rem;
  }
  .glide__arrow--left {
    left: 1rem;
  }
  .single-img__btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: calc(-1rem);
    @include button-variant($btn-background, $white, $btn-background);
  }
}

/* Logo Carousel */
.logo-carousel {
  &__content {
    margin: 1rem;
  }
}

/* Carousel Product */
.carousel-product-item {
  &__image {
    display: grid;
    place-content: center center;
    width: 80%;
    height: 150px;
    margin-inline: auto;
    img {
      display: block;
      width: 100%;
      max-width: 250px;
      height: auto;
      max-height: 150px;
      -o-object-fit: contain;
      object-fit: contain;
    }
  }
  &__info {
    text-align: center;
    margin-top: 1rem;
  }
  &__name {
    line-height: 1.3;
  }
  &__title {
    font-size: 12px;
    a {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  &__contributor {
    margin: 0;
    font-size: 12px;
  }
  &__format {
    font-size: 12px;
  }
  &__purchase {
    text-align: center;
  }
  &__purchase-inner {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  &__purchase-rrp {
    order: 2;
    position: relative;
    font-size: 11px;
    line-height: 20px;
    text-decoration: line-through;
  }
  &__purchase-price {
    order: 1;
    color: #900;
    font-weight: bold;
    line-height: 20px;
  }
  &__availability {
    font-size: 10px;
    font-weight: 400;
    font-weight: bold;
  }
  .buyButton {
    background: #05723f;
    color: #fff;
    font-size: 12px;
    order: 3;
    width: 100%;
    &:hover {
      background: #05723f;
    }
  }
}
.simple-banner {
  padding: 0 !important;
  @include media-breakpoint-up(md) {
    padding-right: calc(var(--bs-gutter-x) * 0.5) !important;
    padding-left: calc(var(--bs-gutter-x) * 0.5) !important;
  }
  img {
    aspect-ratio: 95 / 74;
    @include media-breakpoint-up(md) {
      aspect-ratio: 144 / 47;
    }
  }
}

/* Headers */

.left {
  .col-12 {
    padding: 0 !important;
  }
  .title-block__sub {
    text-align: left !important;
    &:after {
      margin: 0 !important;
    }
  }
}

.title-block {
  &__main {
    text-align: center;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    &-fw {
      position: relative;
      text-align: center;
      padding-top: 1rem;
      padding-bottom: 1rem;
      // &::after {
      //     content: '';
      //     position: absolute;
      //     top: 0;
      //     left: 50%;
      //     transform: translateX( -50%);
      //     height: 100%;
      //     width: 100vw;
      //     background: $subtle-background;
      //     z-index: -1;
      // }
      h1 {
        color: $bg-brand;
        font-weight: 700;
        position: relative;
        z-index: 1;
        margin-bottom: 0;
        display: inline-block;
      }
    }
    h2 {
      color: $bg-brand;
      font-weight: 600;
      position: relative;
      z-index: 1;
      margin-bottom: 0;
      display: inline-block;
    }
    &--link {
      font-weight: 600;
      font-size: 1.25rem;
      display: block;
      text-align: center;
      @include breakpt-lg {
        display: inline-block;
        text-align: left;
      }
      a {
        text-decoration: underline;
        font-size: 1.25rem;
        border-left: 0;
        padding-left: 0;
        font-weight: 400;
        color: $bg-brand;
        @include breakpt-lg {
          border-left: 1px solid $bg-brand;
          margin-left: 20px;
          padding-left: 20px;
        }
      }
    }
  }
  &__sub {
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    h2 {
      color: $bg-brand;
      font-weight: 600;
      margin-bottom: 0;
      display: inline-block;
      font-size: calc(1.275rem + 0.3vw);
      @include breakpt-lg {
        font-size: 1.75rem;
      }
    }
    &--link {
      font-weight: 600;
      font-size: 1.25rem;
      display: block;
      text-align: center;
      @include breakpt-lg {
        display: inline-block;
        text-align: left;
      }
      a {
        text-decoration: underline;
        font-size: 1.25rem;
        border-left: 0;
        padding-left: 0;
        font-weight: 400;
        @include breakpt-lg {
          border-left: 1px solid $bg-brand;
          margin-left: 20px;
          padding-left: 20px;
        }
      }
    }
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      // margin-top: 4px;
      background-color: #bcc8de;
      display: block;
      @include media-breakpoint-up(md) {
        // margin: 4px auto 0;
      }
    }
  }
  &__in-main {
    h2 {
      color: #333;
      font-weight: 600;
      position: relative;
      z-index: 1;
    }
  }
  &__in-sub {
    h4 {
      color: #333;
      font-weight: 600;
    }
  }
}

/* Single Hero */
.single-hero-product {
  &__image {
    margin-bottom: 0.75rem;
    @include media-breakpoint-up(md) {
      margin-bottom: 0;
    }
    img {
      max-height: 220px;
      height: 100%;
      width: 100%;
      @include media-breakpoint-up(sm) {
        max-height: 400px;
        width: initial;
      }
      max-width: 100%;
    }
  }
  &__content {
    text-align: center;
    @include media-breakpoint-up(sm) {
      text-align: left;
    }

    &--title {
      overflow: hidden;
      font-weight: bold;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      -webkit-box-orient: vertical;
      color: $bg-brand;
      margin-bottom: 0.5rem;
      font-size: 22px;
    }
    &--subtitle {
      color: $bg-brand;
      font-size: 22px;
      font-weight: normal;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      -webkit-box-orient: vertical;
      margin-bottom: 0.5rem;
    }
    &--author {
      margin-bottom: 0.5rem;
      a {
        color: $bg-brand;
        font-weight: normal;
        text-decoration: underline;
      }
    }
    &--desc {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      line-clamp: 3;
      -webkit-box-orient: vertical;
      margin-bottom: 0.5rem;
      @include media-breakpoint-up(md) {
        -webkit-line-clamp: 5;
        line-clamp: 5;
      }
      @include media-breakpoint-up(lg) {
        -webkit-line-clamp: 7;
        line-clamp: 7;
      }
    }
    &--price {
      font-size: 0.875rem;
      margin-bottom: 0.5rem;
    }
    &--rrp {
      text-decoration: line-through;
      margin-right: 0.25rem;
    }
    &--current {
      color: $bg-brand;
      font-weight: 600;
      font-size: 1.375rem;
    }
    &--buy-button {
      a {
        margin: 0 auto;
        border-radius: 0;
        @include media-breakpoint-up(md) {
          margin: 0;
        }
        @include button-variant($btn-background, $white, $btn-background);
        max-width: none;
        min-width: 160px;
      }
    }
  }
}

/* Product Block Multi */
.product-item {
  .product-item--img {
    position: relative;
  }
  &:is(#{$product-list--info-tag-selectors}, #{$template--info-tag-selectors})
    .product-item--img {
    --tag-info-bottom: calc(
      var(--jacket-divider-mt, 0rem) - calc(var(--tag-info-fontSize) / 2)
    );
    margin-bottom: 0.5rem;
    @include tag--info-layout();
    --tag-info-fontSize: 14px;
  }
  &:is(#{$product-list--promo-tag-selectors}, #{$template--promo-tag-selectors})
    .product-item--img {
    > a {
      @include tag--promo-layout();
      position: static;
    }
  }
  @include tag-style-map-output();
  @include tag-style-map-output($promo-tags, "promo");
}

.template {
  @include tag-style-map-output();
  @include tag-style-map-output($promo-tags, "promo");
}

.product-list {
  display: flex;
  column-gap: var(--bs-gutter-x);
  flex-direction: column;
  align-items: center;
  align-content: normal;
  text-align: center;
  p {
    margin-bottom: 0;
  }
  @include media-breakpoint-up(md) {
    flex-direction: row;
  }
  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    row-gap: 0.5rem;
    padding: 0;
    text-align: center;
    margin-bottom: 2rem;
    @include media-breakpoint-up(md) {
      width: 100%;
      text-align: center;
      row-gap: 0.75rem;
    }
    &--img {
      width: 100%;
      max-width: 300px;
      height: 220px;
      @include media-breakpoint-up(xl) {
        height: 260px;
      }
      margin: 0 auto;
      a {
        display: flex;
        height: 100%;
      }
      // max-height: 300px;
      @include media-breakpoint-up(md) {
        max-width: 300px;
      }
      img {
        align-self: flex-end;
        max-height: 220px;
        @include media-breakpoint-up(xl) {
          max-height: 260px;
        }
        max-width: 100%;
      }
    }
    &--title {
      overflow: hidden;
      font-weight: 600;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
      color: $bg-brand;
      margin-bottom: 0;
      font-size: 1.25rem;
      line-height: 1;
      height: 2.1em;
      text-wrap: pretty;
    }
    &--author {
      a {
        color: $bg-brand;
        font-weight: 600;
        text-decoration: underline;
      }
    }

    &--price {
      font-size: 0.875rem;
      margin-bottom: 0;
    }
    &--rrp {
      text-decoration: line-through;
      margin-right: 0.25rem;
    }
    &--current {
      color: $bg-brand;
      font-weight: 600;
      font-size: 1.375rem;
    }
    &--buy-button {
      a {
        margin: 0 auto;
        border-radius: 0;
        @include media-breakpoint-up(md) {
          margin: 0;
        }
        @include button-variant($btn-background, $white, $btn-background);
        width: 100%;
        max-width: 100%;
      }
    }
    .pb-template__content--author {
      height: 21px;
      width: 100%;
    }
  }
}
.template--availability {
  font-size: 12px;
  line-height: 14px;
  height: 28px;
  line-clamp: 2;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  @include media-breakpoint-up(md) {
    .clear {
      display: block;
    }
  }
}
.product-carousel {
  &__wrap {
    position: relative;
    @include media-breakpoint-up(md) {
      position: initial;
    }
  }
  &.glide {
    position: initial;
    @include media-breakpoint-up(md) {
      position: relative;
    }
  }
  .glide__arrow {
    margin-top: -50px;
    @include media-breakpoint-up(md) {
      margin-top: -25px;
    }
    &--left {
      left: 10px;
      @include media-breakpoint-up(md) {
        left: -45px;
      }
    }
    &--right {
      right: 10px;
      @include media-breakpoint-up(md) {
        right: -45px;
      }
    }
  }
}

.pb-template {
  display: flex;
  column-gap: var(--bs-gutter-x);
  flex-direction: column;
  align-items: flex-start;
  align-content: normal;
  height: 100%;
  text-align: center;
  &.two-block,
  &.one-block {
    flex-direction: row !important;
    .pb-template__image {
      width: auto !important;
      max-width: 100% !important;
    }
    .pb-template__content {
      text-align: left;
    }
  }
  &.one-block {
    .pb-template__content--btn {
      margin: 0 0 1.5rem 0;
    }
  }
  &.two-block {
    margin-bottom: 1.5rem;
    @include media-breakpoint-up(md) {
      margin-bottom: 0;
    }
  }
  p {
    margin-bottom: 0;
  }
  @include media-breakpoint-up(md) {
    flex-direction: row;
  }
  &__image {
    width: 100%;
    max-width: 300px;
    max-height: 300px;
    margin: 0.75rem 0;
    @include media-breakpoint-up(md) {
      max-width: calc(50% - (var(--bs-gutter-x) * 0.5));
    }
    @include media-breakpoint-up(lg) {
      margin: 0;
      max-height: 400px;
    }
    @include media-breakpoint-up(xl) {
      max-height: 460px;
    }
    /* @include media-breakpoint-up(lg) {
            max-width: calc(25% - (var(--bs-gutter-x) * .75));
        } */
    img {
      @include media-breakpoint-up(md) {
        max-height: 300px;
      }
      @include media-breakpoint-up(lg) {
        max-height: 400px;
      }
      @include media-breakpoint-up(xl) {
        max-height: 460px;
      }
      max-height: 220px;
      width: 100%;
      @include media-breakpoint-up(md) {
        width: initial;
      }
      max-width: 100%;
    }
  }
  &__content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    row-gap: 0.75rem;
    padding: 0;
    text-align: center;
    &--img {
      width: 100%;
      max-width: 300px;
      height: 220px;
      @include media-breakpoint-up(md) {
        max-height: 300px;
      }
      @include media-breakpoint-up(lg) {
        max-height: 400px;
      }
      @include media-breakpoint-up(xl) {
        max-height: 460px;
      }
      margin: 0 auto;
      a {
        display: flex;
        height: 100%;
      }
      // max-height: 300px;
      @include media-breakpoint-up(md) {
        max-width: 300px;
      }
      img {
        align-self: flex-end;
        max-height: 220px;
        @include media-breakpoint-up(md) {
          max-height: 300px;
        }
        @include media-breakpoint-up(lg) {
          max-height: 400px;
        }
        @include media-breakpoint-up(xl) {
          max-height: 460px;
        }
        max-width: 100%;
      }
    }
    &--subtitle {
      color: $bg-brand;
      font-size: 22px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      -webkit-box-orient: vertical;
      margin-bottom: 0;
    }
    &--title {
      overflow: hidden;
      font-weight: 600;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      -webkit-box-orient: vertical;
      color: $bg-brand;
      margin-bottom: 0;
      font-size: 1.5rem;
      &-noclamp {
        -webkit-line-clamp: initial;
        line-clamp: initial;
        text-overflow: ellipsis;
      }
    }
    &--author {
      a {
        color: $bg-brand;
        font-weight: 600;
        text-decoration: underline;
        text-underline-offset: 20%;
      }
    }
    &--desc {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
      @include media-breakpoint-up(md) {
        -webkit-line-clamp: 2;
        line-clamp: 2;
      }
      @include media-breakpoint-up(lg) {
        -webkit-line-clamp: 4;
        line-clamp: 4;
      }
      &-noclamp {
        -webkit-line-clamp: initial;
        line-clamp: initial;
        text-overflow: ellipsis;
      }
      a {
        color: #002f87;
        text-decoration: underline;
      }
    }
    &--price {
      margin-top: auto;
      margin-bottom: 0;
      font-size: 0.875rem;
    }
    &--rrp {
      text-decoration: line-through;
      margin-right: 0.25rem;
    }
    &--current {
      color: $bg-brand;
      font-weight: 600;
      font-size: 1.375rem;
    }
    &--buy-button {
      a {
        margin: 0 auto;
        border-radius: 0;
        @include media-breakpoint-up(md) {
