/* Circle Image Block */

.c-img-block {
  text-align: center;
  &__img {
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 1rem;
    img {
      transform: scale(1);
      @include transition(transform 0.35s ease);
    }
  }
  &__title {
    color: $bg-brand;
    font-weight: 600;
  }
  &:hover {
    img {
      transform: scale(1.1);
    }
  }
}

.bg-c-img-block {
  text-align: center;
  overflow: hidden;
  &__img {
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 1rem;
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    @include transition(background-color 0.35s ease);
    img {
      margin: 0;
      height: auto;
      position: absolute;
      box-shadow: 0px 0px 20px 0px rgba(1, 48, 136, 0.35);
      -webkit-box-shadow: 0px 0px 20px 0px rgba(1, 48, 136, 0.35);
      -moz-box-shadow: 0px 0px 20px 0px rgba(1, 48, 136, 0.35);
    }
    &.sm {
      background-color: #00e4e2;
      img {
        width: 60%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
        @include transition(transform 0.35s ease);
      }
    }
    &.lg-jacket {
      --circle-colour: #{($subtle-background)};
      background-color: var(--circle-colour);
      aspect-ratio: 1 / 1;
      img {
        width: 75%;
        bottom: 80%;
        left: 50%;
        transform: translate(-50%, 100%) scale(1);
        @include transition(transform 0.35s ease);
      }
    }
    &.jacket {
      --circle-colour: #{($subtle-background)};
      background-color: var(--circle-colour);
      aspect-ratio: 1 / 1;
      > div {
        width: 80%;
        bottom: 80%;
        left: 45%;
        transform: translate(-50%, -50%) rotate(-25deg);
        transform-origin: top center;
        position: absolute;
        img {
          width: 80%;
          transform-origin: top center;
          transform: scale(1);
          @include transition(transform 0.35s ease);
        }
      }
    }
  }
  &__title {
    color: $bg-brand;
    font-weight: 600;
    font-size: 1.125rem;
    display: -webkit-box;
    line-clamp: 2;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    text-wrap: pretty;
    overflow: hidden;
    margin: 0 0 1rem;
  }
  &:hover {
    .bg-c-img-block__img {
      --circle-hover-colour: #{($bg-brand)};
      background-color: var(--circle-hover-colour);
      &.jacket > div img {
        transform: scale(1.1);
      }
      &.lg-jacket img {
        transform: translate(-50%, 100%) scale(1.1);
      }
      &.sm img {
        transform: translate(-50%, -50%) scale(1.1);
      }
    }
  }
}

/* Four up blocks */

.four-up {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  padding-bottom: 55px;
  &:hover {
