/* Slider Custom */
/* Slider Custom -- Glide Arrows & Bullets */
.glide {
  height: auto;
  &__track {
    position: relative;
  }
  &__arrow {
    background: #ddd;
    top: 50%;
    margin-top: -25px;
    width: 40px;
    height: 50px;
    opacity: 0.65;
    position: absolute;
    text-align: center;
    padding: 0;
    border: 0;
    &:after {
      content: "";
      display: inline-block;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
      width: 17px;
      height: 17px;
      position: relative;
      top: 3px;
      @include transition(border-right 0.35s ease, border-left 0.35s ease);
    }
    &:hover:after {
      border-right: 1px solid #005bff;
      border-bottom: 1px solid #005bff;
    }
    &--left {
      left: 0;
      &:after {
        transform: rotate(-225deg);
        left: 4px;
      }
    }
    &--right {
      right: 0;
      &:after {
        transform: rotate(-45deg);
        right: 4px;
      }
    }
  }
  &__bullets {
    width: 100%;
    text-align: center;
  }
  &__bullet {
    width: 11px;
    height: 12px;
    border: 0;
    display: inline-block;
    border-radius: 50%;
    background: #dbdbdb;
    margin: 8px 3px 3px;
    &--active {
      background: #1c4aa0;
    }
  }
}

