// Ten Up Image Carousel
.product-carousel--ten-up {
  .glide__slide {
    display: grid;
    place-content: center center;
    padding: 1rem;

    * {
      grid-column: 1 / -1;
      grid-row: 1 / -1;
    }

    a {
      display: block;
      max-width: 100px;
      max-height: 100px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      filter: drop-shadow(0 0 0.25rem rgba(0, 0, 0, 0.4));
      transition: 150ms ease-in-out;
    }

    // Product Carousel
    &:has(a + div) {
      a {
        z-index: 2;
      }

      .product__background {
        background: var(--product-background, #003087);
        width: 100px;
        height: 100px;
        border-radius: 50%;
      }
    }

    // Image Carousel
    &:not(:has(a + div)) {
      a {
        border-radius: 50%;
        overflow: hidden;

        &:hover {
          img {
            transform: scale(1.2);
          }
        }
      }
    }
  }
}
