.manualForm{
  .custom-form {
    margin-bottom: 1rem;
    label{
      display: block;
      margin-top: .5rem;
      color: #003087;
      font-weight: bold;
      grid-row-start: 1;
    }
    .questions {
      display: block;
      color: #003087;
      font-weight: bold;
      margin-bottom: 0.25rem;
    }
    input[type="text"], input[type="email"], textarea{
      display: block;
      margin: .25rem 0 .5rem;
      border: 2px solid #bcc8de;
      padding: .375rem .75rem;
      font-size: 1rem;
      font-weight: 400;
      line-height: 1.5;
      color: var(--bs-body-color);
      background-color: var(--bs-body-bg);
      background-clip: padding-box;
      border-radius: 0;
      width: 100%;
      grid-row-start: 1;
    }
    input[type="checkbox"]{
      width: 24px;
      height: 24px;
      border: 2px solid #bcc8de;
    }
    &__radioGroup{
      .answers{
        display: flex;
        gap: 1rem;
        justify-content: flex-start;
        flex-wrap: nowrap;
        flex-direction: row;
        align-items: flex-start;
        > div{
          flex: none;
        }
      }
    }
    &__radioOption{
      flex: none;

      input{
        display: block;
        float: left;
        margin-right: .5rem;
        width: 24px;
        height: 24px;
        border-color: #bcc8de;
      }
      label{
        margin: 0;
        display: block;
        float: left;
        color: var(--bs-body-color);
        font-weight: normal;
        line-height: 24px;
      } 
    }
    &.checkbox {
      display: grid;
      grid-template-columns: [controls] auto [labels] 1fr;
      grid-auto-flow: row;
      grid-row-gap: 0rem;
      grid-column-gap: 1.5rem;
      @include breakpt-md {
        grid-template-columns: [labels] auto [controls] 1fr;
      }
      label{
        grid-column: labels;
        margin-top: 0;
      }
      input{
        grid-column: controls;
      }
    }
    @include breakpt-md {
      display: grid;
      grid-template-columns: [labels] auto [controls] 1fr;
      grid-auto-flow: row;
      grid-row-gap: 0rem;
      grid-column-gap: 1.5rem;
      &.eAdd,
      .pass {
        .field-validation-error {
          grid-area: 2 / 2;
        }
      }
      label, .questions {
        width: var(--formLabelSpace);
         @include breakpt-md {
           width: calc(var(--formLabelSpace) * 1.5);
         }
      }
      .emailAddress {
        margin: 0.5rem 0;
      }
      .field-validation-valid {
        padding-left: 0;
      }
      .field-validation-error {
        grid-column: controls;
      }
      .custom-control {
        padding-left: 1.5rem;
      }
      .custom-control,
      .field-validation-valid {
        position: relative;
        display: block;
        grid-column: controls;
        grid-row: auto;
        border: none;
      }

    }
  }
    #formSubmitButton{
      @include breakpt-md {
        margin-left: calc((var(--formLabelSpace) * 1.5) + 1.5rem);
      }
      input[type="submit"]{
        background-color: #002f87;
        color: #fff;
        font-weight: 600;
        padding: 6px 18px;
        text-align: center;
